.calc__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    margin: auto;  
}

.calc-settings__list {
    width: 100%;
    padding: 10px;
    border: 1px solid white;
    border-radius: 20px;
}

.calc-settings__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.calc-settings__item span{
    margin: 5px 5px;
    font-size: 15px;
}
.calc-settings__item input, .calc-settings_choice {
    text-align: center;
    align-items: center;
    margin-left: auto;
    width: 200px;
    background-color: white;
    color: black;
    border: 2px solid black;
    transition: border-color 0.2s ease-in;
}

.calc-settings__item input:focus-visible {
    border-color: orange;
}


.calc__btn {
    display: flex;
    justify-content: center;
    align-items: center;
}

.calc__btn button {
    margin: 20px;
    padding: 5px;
    width: 100px;
    background-color: white;
    color: black;
    border-radius: 50px;
    border: 1px solid black;

}

.calc__btn button:hover {
    transform: translateY(-0.25em);
    box-shadow: 0 0 0.5em 0em #5e5e5e;
    border-color: #979695;
}

.calc__comment {
    margin-top: 40px;
    text-align: center;
}
.calc__comment a {
    color: white;
    text-decoration: underline;
}

/* Manual */

.manual{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.manual p {
    margin: 5px 0;
    text-indent: 1.5em;
    font-size: 20px;
}

.manual-list__item {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
    font-size: 18px;
}

.manual-list__item span {
    margin: 5px 0;
    text-indent: 1.5em;
}
.manual-list__item span strong {
    margin-right: 10px;
    font-size: 22px;
}

.manual-list__item div strong {
    margin-right: 10px;
    font-size: 22px;
}

.manual-list__formulas {
    margin: auto !important;
    font-size: 22px !important;
}

@media (max-width: 620px) {
    .calc__wrapper, .calc__wrapper {
        width: 90%;
    }
    .calc-settings__item {
        flex-direction: column;
        justify-content: center;
    }
    
    .calc-settings__item span {
        text-indent: 0.5em;
    }

    .calc-settings__item input, .calc-settings_choice {
        margin-left: unset;
    }
}