.footer {
    background-color: black; 
    padding: 20px 0 10px;
    color: white;
    margin-top: auto;
    border-top: 0.5px solid white;
}

.footer__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 27px;
}

.social {
    display: flex;
    column-gap: 30px;
    align-content: center;
}

.social__item a img {
    width: 30px;
    height: 30px;
}

.copyright {
    font-size: 16px;
}

