.header {
    padding: 40px;
    min-height: 100px;
    display: flex;
    justify-content: center;
    text-align: center;
}

.header__title {
    font-size: 30px;
    font-weight: 700;
    line-height: 1.4;
}

.header__title span {
    font-size: 14px;
}

@media (max-width: 620px) {
    .header__title {
        font-size: 16px;
    }
}