.nav {
    padding: 20px 0;
    background-color: black;
    color: white;
    letter-spacing: normal;
    border-bottom: 0.5px solid white;
}

.nav-row {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    column-gap: 30px;
    row-gap: 20px;
    flex-wrap: wrap;
}

.logo {
    margin-right: auto;
    width: 100px;
    height: 100px;
}

.nav-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 40px;
    row-gap: 10px;
    font-size: 16px;
    font-weight: 500;
    font-family: "Poppins", sans-serif;
}

.nav-list__link {
    color: white;
    transition: opacity 0.2s ease-in;
}

.nav-list__link:hover {
    opacity: 0.4;
}

@media (max-width: 620px) {

    .nav-row{
       justify-content: center;
       row-gap: 0px;
    }
 
    .logo {
        margin-left: auto;
    }
 }